.card {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  width: 300px;
  margin: 10px 70px;
  border-radius: 20px;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.5);
}

.card img {
  width: 6rem;
  height: 6rem;
  transition: all 0.2s ease-in-out;
}

.card p {
  height: 6rem;
}

.card:hover img {
  transform: scale(1.2);
}
